<!-- props -->
<template>
  <div :class="{ 'is-winner': isWinner, 'is-loser': isLoser }">
    <v-card class="mx-auto pa-0" :class="{ selected }">
      <v-progress-circular v-if="!imgLoaded" indeterminate color="primary"></v-progress-circular>
      <!-- // -->
      <v-img
        v-if="!loadError"
        @error="errorImage"
        @load="onLoad"
        class="image mx-auto"
        :src="hero.image.url"
      ></v-img>
      <!-- // -->
      <v-img
        v-if="loadError"
        @load="onLoad"
        class="image mx-auto"
        src="@/assets/img_noImage@2x.png"
      ></v-img>

      <p class="text-center ma-1 font-weight-medium" v-if="showName">
        {{ hero.name }}
      </p>
      <div class="powerstats" v-if="showDetail">
        <div class="powerstats__wrapper wrapper">
          <p class="text-center mt-2">{{ hero.biography.publisher }}</p>
          <p class="text-center mt-2">{{ hero.biography.firstAppearance }}</p>
          <ul class="powerstats__listFrame pl-0">
            <li class="text-center mt-2 mb-2" v-if="showName">
              {{ hero.powerstats.intelligence }}
            </li>
            <li class="text-center mt-2 mb-2" v-if="showName">
              {{ hero.powerstats.strength }}
            </li>
            <li class="text-center mt-2 mb-2" v-if="showName">{{ hero.powerstats.speed }}</li>
            <li class="text-center mt-2 mb-2" v-if="showName">
              {{ hero.powerstats.durability }}
            </li>
            <li class="text-center mt-2 mb-2" v-if="showName">{{ hero.powerstats.power }}</li>
            <li class="text-center mt-2 mb-2" v-if="showName">
              {{ hero.powerstats.combat }}
            </li>
          </ul>
        </div>
      </div>

      <div class="d-flex">
        <div v-if="selectable">
          <v-btn v-if="selected" depressed color="primary" @click="onSelect" class="ma-1">
            
          </v-btn>
          <v-btn v-if="!selected" depressed color="primary" @click="onSelect" class="ma-1">
            
          </v-btn>
        </div>
        <!-- <v-btn v-if="bookmarkable" depressed color="primary">
          
        </v-btn> -->
      </div>
      <!-- <router-link :to="`/character/${id}`"></router-link> -->
    </v-card>
    <!-- <button @click="addd">addd</button> -->

    <div v-if="showBarChart">
      <HorizontalBarChart
        :chartData="chartData"
        :options="options"
        class="mt-5"
        :height="200"
        :width="200"
      />
      <p class="text-center text-h6 primary--text">{{ hero.totalStats }}</p>
    </div>
  </div>
</template>

<script>
import HorizontalBarChart from "@/components/HorizontalBarChart.vue";

export default {
  name: "Hero",
  components: {
    HorizontalBarChart
  },
  props: {
    hero: { type: Object },
    selectable: { type: Boolean, default: false },
    bookmarkable: { type: Boolean, default: false },
    selected: { type: Boolean, default: false },
    bookmarked: { type: Boolean, default: false },
    showName: { type: Boolean, default: true },
    showBarChart: { type: Boolean, default: false },
    showDetail: { type: Boolean, default: false }
  },
  data() {
    return {
      status: [],
      isNull: false,
      imgLoaded: false,
      loadError: false,
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [
            {
              ticks: {
                min: 0, // 
                max: 1000, // 
                stepSize: 5 // 
              },
              stacked: true
            }
          ],
          yAxes: [
            {
              stacked: true
            }
          ]
        }
      }
    };
  },
  computed: {
    chartData() {
      const datacollection = {
        datasets: [
          {
            label: "",
            backgroundColor: "blue",
            data: [this.hero.totalStats]
          }
        ]
      };
      if (this.powerUp !== 0) {
        datacollection.datasets.push({
          label: "",
          backgroundColor: "red",
          data: [this.hero.powerUp]
        });
      }
      return datacollection;
    },
    isWinner() {
      return this.hero.winner;
    },
    isLoser() {
      return this.hero.loser;
    }
  },
  methods: {
    onSelect() {
      this.$emit("select", this.hero);
    },
    onLoad() {
      this.imgLoaded = true;
    },
    errorImage() {
      this.loadError = true;
    }
  }
};
</script>
